<template>
    <section-block title="Редактирование товара">
        <div class="row">
            <div class="col-lg-6 acenter">
                <div class="space10"></div>
                <el-upload
                        action="/upload/images/"
                        list-type="picture-card"
                        :file-list="form.images"
                        accept="image/*"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                    <i class="fa fa-plus"></i>
                </el-upload>
                <el-dialog v-model="dialogVisible" size="tiny">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>

            </div>
            <div class="col-lg-18 lborder">
                <div class="form">
                    <div class="form-line">
                        <span class="input">
                            <el-select v-model="form.category" placeholder="Выберите категорию" :class="{'input-filled': form.category}">
                                <el-option-group
                                        v-for="group in categories"
                                        :key="group.label"
                                        :label="group.label">
                                    <el-option
                                            v-for="item in group.options"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input" :class="{'input-filled': form.name}">
                            <input class="input-field" type="text" v-model="form.name"/>
                            <label class="input-label">
                                <span class="input-label-content">Название товара</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line" :class="{'input-filled': form.description}">
                        <span class="input">
                            <auto-text class="input-field" v-model="form.description" :autosize="{minRows: 3, maxRows: 10}"></auto-text>
                            <label class="input-label">
                                <span class="input-label-content">Описание товара</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line">
                        <span class="input input-filled">
                            <input class="input-field" type="number" v-model="form.price" min="1"/>
                            <i class="fa fa-plus input-number" @click="form.price++"></i>
                            <i class="fa fa-minus input-number" @click="form.price--"></i>
                            <label class="input-label">
                                <span class="input-label-content">Стоимость</span>
                            </label>
                        </span>
                    </div>
                    <div v-show="form.limitations" class="form-line" :class="{'input-filled': form.flaw}">
                        <span class="input">
                            <auto-text class="input-field" v-model="form.flaw" :autosize="{minRows: 3, maxRows: 10}"></auto-text>
                            <label class="input-label">
                                <span class="input-label-content">Описание дефектов</span>
                            </label>
                        </span>
                    </div>
                    <div class="form-line acenter">
                        <button class="btn btn-primary" @click="save">Сохранить</button>
                        <button class="btn btn-default" @click="back">Назад</button>
                    </div>
                </div>
            </div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import autoText from '../global/auto-textarea.vue'

    export default {
        name: 'goodPage',
        components: {
            sectionBlock,
            autoText
        },
        data() {
            return {
                dialogImageUrl: '',
                dialogVisible: false,
                categories: [
                    {
                        label: 'Бытовая электроника',
                        options: [
                            {
                                label: 'Аудио и видео',
                                value: 1
                            },
                            {
                                label: 'Игры, приставки и программы',
                                value: 2
                            },
                            {
                                label: 'Настольные компьютеры',
                                value: 3
                            },
                            {
                                label: 'Оргтехника и расходники',
                                value: 4
                            },
                            {
                                label: 'Планшеты и электронные книги',
                                value: 5
                            },
                            {
                                label: 'Телефоны',
                                value: 6
                            },
                            {
                                label: 'Товары для компьютера',
                                value: 7
                            },
                            {
                                label: 'Фототехника',
                                value: 8
                            },
                        ],
                    },
                ],
                form: {
                    category: 1,
                    name: 'Тест',
                    description: 'Описание',
                    type: 'buy',
                    price: 1000,
                    used: false,
                    limitations: true,
                    flaw: '123',
                    visible: false,
                    images: [
                        {name: '3378714405.jpg', url: 'https://05.img.avito.st/640x480/3378714405.jpg'},
                        {name: '3378714837.jpg', url: 'https://37.img.avito.st/640x480/3378714837.jpg'},
                        {name: '3378715031.jpg', url: 'https://31.img.avito.st/640x480/3378715031.jpg'},
                        {name: '3378715501.jpg', url: 'https://01.img.avito.st/640x480/3378715501.jpg'},
                        {name: '3378715282.jpg', url: 'https://82.img.avito.st/640x480/3378715282.jpg'},
                    ]
                }
            }
        },
        methods: {
            save() {
                console.log('saved');
            },
            back() {
                this.$router.go(-1);
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            }
        }
    }
</script>


