<template>
    <div class="row">
        <div class="col-lg-12">
            <section-block title="Мои товары" :buttons="[{name: 'Добавить товар', url: '/goods/create', icon: 'fa-plus-square-o'}]">
                <template v-if="goods.length">
                    <div class="good-line" v-for="good in goods">
                        <router-link :to="'/goods/' + good.id">
                            <div class="preview">
                                <img :src="good.preview" alt="Good"><br>
                                <div class="statuses hidden-sm hidden-md hidden-lg hidden-xl hidden-xxl">
                                    <el-tooltip content="Бывший в употреблении" placement="top" v-if="good.used">
                                        <div class="status used"><i class="fa fa-history"></i></div>
                                    </el-tooltip>
                                    <el-tooltip :content="'Оценка эксперта: ' + good.evaluation + ' ₽'" placement="top" v-if="good.evaluation != '0'">
                                        <div class="status evaluation"><i class="fa fa-users"></i></div>
                                    </el-tooltip>
                                </div>
                            </div>
                            <div class="good-content">
                                <div class="statuses hidden-xs">
                                    <el-tooltip content="Бывший в употреблении" placement="top" v-if="good.used">
                                        <div class="status used"><i class="fa fa-history"></i></div>
                                    </el-tooltip>
                                    <el-tooltip :content="'Оценка эксперта: ' + good.evaluation + ' ₽'" placement="top" v-if="good.evaluation != '0'">
                                        <div class="status evaluation"><i class="fa fa-users"></i></div>
                                    </el-tooltip>
                                </div>
                                <!--<div class="price hidden-sm hidden-md hidden-lg hidden-xl hidden-xxl"><span>{{good.price}}</span> ₽</div>-->
                                <div class="good-title">{{good.name}}</div>
                                <div class="good-category">{{good.category}}</div>
                                <div class="good-description">
                                    <div class="listed">
                                        <b class="hidden-xs">Тип сделки:</b> {{dealTypes[good.dealType]}}
                                    </div>
                                    <div class="listed">
                                        <b class="hidden-xs">Добавлен:</b> {{good.date | moment('Do MMMM YYYY')}}
                                    </div>
                                </div>
                                <div class="actions">
                                    <div class="price"><span>{{good.price}}</span> ₽</div>
                                </div>
                            </div>
                        </router-link>
                    </div>
                </template>
                <div v-else class="acenter noData">У Вас нет добавленных товаров</div>
            </section-block>
        </div>
        <div class="col-lg-12">
            <section-block title="Активные сделки">
                <template v-if="deals.length">
                    <div class="deal-line row" :class="deal.state" v-for="deal in deals">
                        <div class="item col-sm-10 col-xs-24">
                            <router-link :to="'/goods/' + deal.item.id">
                                {{deal.item.name}}
                            </router-link>
                        </div>
                        <div class="participant col-sm-8 col-xs-16">
                            <router-link :to="'/user/' + deal.participant.id">
                                {{deal.participant.fullname}}
                            </router-link>
                        </div>
                        <div class="cost col-sm-4 col-xs-6 aright"><span>{{deal.cost}} ₽</span></div>
                        <div class="link col-xs-2 col-xs-2 aright">
                            <router-link :to="'/deal/' + deal.id">
                                <i class="fa fa-angle-double-right"></i>
                            </router-link>
                        </div>
                    </div>
                </template>
                <div v-else class="acenter noData">У Вас нет активных сделок</div>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'

    export default {
        name: 'goods',
        components: {
            sectionBlock
        },
        data() {
            return {
                dealTypes: {
                    buy: 'продажа',
                    rent: 'прокат',
                    service: 'сервис',
                    store: 'хранение',
                    selling: 'реализация'
                },
                goods: [],
                deals: []
            }
        },
        methods: {

        },
        mounted() {
            //Загрузка товаров
            this.$http.get('/api/mygoods').then(response => {
                this.goods = response.body;
            }, response => {
                console.log(response)
            });
            //Загрузка сделок
            this.$http.get('/api/deals/goods').then(response => {
                this.deals = response.body;
            }, response => {
                console.log(response)
            });
        }
    }
</script>

<style lang="sass" src="@/assets/sass/pages/goods.sass"></style>


