<template>
    <div>
        <div class="row">
            <div class="col-xxl-18 col-xl-16 col-lg-16 col-md-14 col-sm-24">
                <transition :enter-active-class="transitionPageIn" :leave-active-class="transitionPageOut" :duration="500" mode="out-in">
                    <router-view :good="good"></router-view>
                </transition>
            </div>
            <div class="col-xxl-6 col-xl-8 col-lg-8 col-md-10 col-sm-24">
                <section-block>
                    <div class="price-block acenter">
                        <span><i class="fa fa-angle-double-right"></i>&nbsp;&nbsp;&nbsp;{{good.cost}} ₽</span>
                        <button class="btn btn-success pull-right">Купить</button>
                    </div>
                </section-block>
                <section-block title="Действия">
                    <el-checkbox v-model="good.visible" style="margin-top: 10px;">Показывать товар другим участникам</el-checkbox>
                    <router-link :to="'/goods/'+good.id+'/edit'">
                        <button class="btn btn-special-primary">Редактировать товар</button>
                    </router-link>
                    <button class="btn btn-danger btn-full btn-special" @click="deleteDialog = true">Удалить товар</button>
                </section-block>
                <section-block title="Владелец" class="hidden-zero hidden-xs hidden-sm">
                    <div class="profile-block">
                        <img :src="good.owner.avatar" alt="" class="img-circle">
                        <div class="fullname">{{good.owner.firstname}} {{good.owner.surname}}</div>
                        <div class="rating"><el-rate
                                v-model="good.owner.rate"
                                disabled
                                text-color="#ff9900">
                        </el-rate></div>
                    </div>
                    <div class="block-lines">
                        <div class="block-line">
                            Зарегистрирован <span class="pull-right">{{good.owner.created_at | moment('Do MMMM YYYY')}}</span>
                        </div>
                        <div class="block-line">
                            Группа <span class="pull-right">{{good.owner.group}}</span>
                        </div>
                    </div>
                    <router-link :to="'/chat/'+good.owner.id">
                        <button class="btn btn-special-primary">Отправить сообщение</button>
                    </router-link>
                </section-block>
            </div>
        </div>
        <el-dialog title="Удаление товара" v-model="deleteDialog" size="tiny">
            <div class="question">Вы действительно хотите удалить товар?</div>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-danger btn-small">Удалить товар</button>
                <button class="btn btn-primary btn-small" @click="deleteDialog = false">Отменить</button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'goodPage',
        components: {
            sectionBlock
        },
        data() {
            return {
                good: {
                    owner: {}
                },
                deleteDialog: false,
                transitionPageIn: 'animated05 fadeIn',
                transitionPageOut: 'animated05 fadeOut'
            }
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname']),
        },
        methods: {

        },
        mounted() {
            //Загрузка данных о товаре
            this.$http.get('/api/good/'+this.$route.params.id).then(response => {
                this.good = response.body;
            }, response => {
                console.log(response)
            });
        }
    }
</script>

<style>
    .price-block {
        line-height: 35px
    }
    .price-block button {
        margin-top: 2px;
    }
    .price-block span {
        font-size: 17px;
        font-weight: 400;
        color: #3c8dbc;
        display: inline-block;
        margin-top: 2px;
    }
    .price-block span i {
        position: absolute;
        font-size: 40px;
        left: 0px;
        top: 8px;
    }
</style>


