<template>
    <div class="row">
        <div class="col-xs-24">
            <section-block :title="'Товар &quot;'+good.name+'&quot;'">
                <div id="gallery" class="acenter">
                    <a :href="url" v-for="url in good.image">
                        <img :src="url"/>
                    </a>
                </div>
                <div class="information">
                    <div class="good-title">{{good.name}}</div>
                    <div class="good-cat">{{good.category}}</div>
                </div>
                <div class="owner hidden-md hidden-lg hidden-xl hidden-xxl">
                    <router-link :to="'/chat/'+ good.owner.id">
                        <button class="btn btn-primary pull-right"><i class="fa fa-send"></i></button>
                    </router-link>
                    <router-link :to="'/user/'+ good.owner.id">
                        <img :src="good.owner.avatar" alt="Avatar" class="img-circle">
                        <div class="fullname">{{good.owner.firstname}} {{good.owner.surname}}</div>
                    </router-link>
                    <div class="rating"><el-rate
                            v-model="good.owner.rate"
                            disabled
                            text-color="#ff9900">
                    </el-rate></div>
                </div>
                <div class="information">
                    <div class="good-used">
                        <b>Состояние:</b>
                        <span v-if="good.used">Бывший в употреблении</span>
                        <span v-else>Новый</span>
                    </div>
                    <div class="good-limitations">
                        <b>Дефекты:</b>
                        <span v-if="good.limitations">{{good.flaw}}</span>
                        <span v-else>не найдены</span>
                    </div>
                    <div class="good-description">
                        <b>Описание:</b> {{good.descr}}
                    </div>
                </div>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import lg from 'lightgallery.js/dist/js/lightgallery.min'

    export default {
        name: 'goodPage',
        components: {
            sectionBlock,
        },
        props: ['good'],
        data() {
            return {
            }
        },
        methods: {

        },
        mounted() {
            lightGallery(document.getElementById('gallery'));
        }
    }
</script>

<style lang="scss" src="@/assets/sass/light-gallery/lightgallery.scss"></style>

<style lang="sass" src="@/assets/sass/pages/goodInfo.sass"></style>


